import React from "react";

function Products({ title, price, description, category, image, rate, count }) {

  return (
    <div className="col-6 p-3 hover:shadow-lg hover:shadow-gray-300">
      <h2>{title}</h2>
      <h5>{price}</h5>
      <h5>{description}</h5>
      <h5>{category}</h5>
      <img src={image} className="w-50 h-50 img-fluid mx-auto" alt="" />
      <h5>{rate}</h5>
      <h5>{count}</h5>
    </div>
  );
}

export default Products;
