import React from 'react'
import Menuitemt from './Menuitemt'

function Menu({menu}) {
  return (
    <nav className="navbar navbar-expand-sm bg-dark navbar-dark position-fixed col-12 z-10" id='up'>
  <div className="container-fluid ">
      <ul className="navbar-nav w-100 justify-content-center">
        {menu?.map((item)=>{
            return     <Menuitemt key={item.id} name={item.name} link={item.link} dropdown={item.dropdown} />   
        })}
      </ul>
  </div>
</nav>
  )
}

export default Menu