import React, { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/effect-cards";

import "./styles.css";

// import required modules
import { Autoplay, EffectCards } from "swiper/modules";

export default function Slider({ slider,shoes}) {
  // console.log(slider);
  return (
    <div className="col-12 bg-gray-200 pt-5">
      <div className="row w-100">
    <div className="col-6 p-3">
      <Swiper
        autoplay={{
          delay: 500,
          disableOnInteraction: false,
        }}
        effect={"cards"}
        grabCursor={true}
        modules={[EffectCards, Autoplay]}
        className="mySwiper"
      >
        {slider?.map((item) => {
          return (
            <SwiperSlide key={item.id}>
              <img
                src={item.image}
                className=" w-100 h-100 img-fluid"
                alt={item.alt}
              />
            </SwiperSlide>
          );
        })}
      </Swiper>
    </div>
    <div className="col-6 p-3">
      <Swiper
        autoplay={{
          delay: 600,
          disableOnInteraction: false,
        }}
        effect={"cards"}
        grabCursor={true}
        modules={[EffectCards, Autoplay]}
        className="mySwiper"
      >
        {shoes?.map((elem) => {
          return (
            <SwiperSlide key={elem.id}>
              <img
                src={elem.src}
                className=" w-100 h-100 img-fluid"
                alt={elem.alt}
              />
            </SwiperSlide>
          );
        })}
      </Swiper>
    </div>
    </div>
    </div>
  );
}
