import { useEffect, useState } from "react";
import "./App.css";
import axios from "axios";
import Menu from "../Menu/Menu";
import Slider from "../Slider/Slider";
import Products from "../Products/Products";
import Footer from "../Footer/Footer";
function App() {
  const [menu, setMenu] = useState(null);
  const [slider, setSlider] = useState(null);
  const [shoes, setShoes] = useState(null);
  const [products, setProducts] = useState(null);
  const fetchMenu = async () => {
    try {
      let res = await axios("https://mohammad-anvari.github.io/project-react/db.json");
      setMenu(res.data.menu);
    } catch (error) {
      console.log(error.message);
    }
  };
  const fetchSlider = async () => {
    try {
      let res = await axios("https://mohammad-anvari.github.io/project-react/db.json");
      setSlider(res.data.slider);
    } catch (error) {
      console.log(error.message);
    }
  };
  const fetchShoes = async () => {
    try {
      let res = await axios("https://mohammad-anvari.github.io/project-react/db.json");
      setShoes(res.data.shoes);
    } catch (error) {
      console.log(error.message);
    }
  };
  const fetchProducts = async () => {
    try {
      let res = await axios("https://fakestoreapi.com/products");
      setProducts(res.data);
    } catch (error) {
      console.log(error.message);
    }
  };
  useEffect(() => {
    fetchMenu();
    fetchSlider();
    fetchShoes();
    fetchProducts();
  }, []);
  return (
    <div className="container-fluid px-0">
      {/* Menu */}
<div>      <Menu menu={menu} /></div>
      {/* Menu */}
      {/* slider */}
      <div className="w-100">
        <Slider slider={slider} shoes={shoes} />
      </div>
      {/* slider */}
      {/* Products */}
      <div className="col-10 offset-1 mt-5">
        <div className="row justify-content-around">
          {products?.map((elem) => {
            return (
              <Products
                title={elem.title}
                price={elem.price}
                description={elem.description}
                category={elem.category}
                image={elem.image}
                rate={elem.rate}
                count={elem.count}
                key={elem.id}
              />
            );
          })}
        </div>
      </div>
      {/* Products */}
      {/* Footer */}
<div className="mt-5">
  <Footer/>
</div>
      {/* Footer */}
      </div>
  );
}

export default App;
