import React from 'react'

function Menuitemt({name,link,dropdown}) {
  return dropdown.length?(
<li className="nav-item dropdown">
  <a className="nav-link dropdown-toggle" href={link} role="button" data-bs-toggle="dropdown">{name}</a>
  <ul className="dropdown-menu ">
    {dropdown?.map((elem)=>{
        return <li key={elem.id}><a className="dropdown-item" href={elem.link}>{elem.name}</a></li>
    })}
  </ul>
</li>
  ):(    <li className="nav-item capitalize">
  <a className="nav-link" href={link}>{name}</a>
</li>)
  
}

export default Menuitemt